<template>
	<button v-on:click="click" :class="finalClassName">
		<slot></slot>
	</button>
</template>

<script>
const themeToClassMap = {
	white: "white-button",
	red: "red-button",
	"light-blue": "light-blue-button",
	blue: "blue-button",
	"dark-blue": "dark-blue-button",
	unstyled: "unstyled-button",
};


export default {
	name: "app-button",
	props: ["theme", "click", "className"],
	computed: {
		finalClassName() {
			return `app-button ${themeToClassMap[this.theme || "white"]} ${this.className}`;
		},
	},
};
</script>

